import React from 'react'
import { Row, Col, Container } from 'reactstrap'
import ReactPlayer from 'react-player'
import './style.scss'

function GioiThieu1(props) {
  return (
      <Container>
        <Row>
          <Col xs={12} className='text-center'>
          <div className={'videoWrapper'}>
            <ReactPlayer url={'https://youtu.be/RGxzTJkmeGs'}
              controls
              className={'video'}
              width='100%'
              height='100%'
            />
          </div>
          </Col>
          <Col md={12}>
            <h5 style={{ marginTop: '40px' }}><b> TỔNG QUAN </b></h5>
            <p>
            <b>Cục Quản lý Y, Dược cổ truyền có tư cách pháp nhân, có con dấu, tài khoản riêng và có trụ sở làm việc tại thành phố Hà Nội. Tổ chức bộ máy và cơ chế hoạt động như sau:</b>
            <br />1. Lãnh đạo Cục: gồm Cục trưởng và không quá 03 Phó Cục trưởng.

            <br />Cục trưởng và các Phó Cục trưởng do Bộ trưởng Bộ Y tế bổ nhiệm, miễn nhiệm, cách chức theo các quy định của pháp luật.

            <br />Cục trưởng chịu trách nhiệm trước Bộ trưởng Bộ Y tế và trước pháp luật về toàn bộ hoạt động của Cục. Các Phó Cục trưởng giúp việc cho Cục trưởng và được Cục trưởng giao phụ trách một số công tác, chịu trách nhiệm trước Cục trưởng và trước pháp luật về các nhiệm vụ được giao.

            <br />2. Cơ cấu tổ chức:

            <br />&nbsp;&nbsp; a) Văn phòng Cục;

            <br />&nbsp;&nbsp;  b) Phòng Quản lý Y cổ truyền;

            <br />&nbsp;&nbsp; c) Phòng Quản lý Dược cổ truyền;

            <br />&nbsp;&nbsp; d) Phòng Quản lý hành nghề Y, Dược cổ truyền;

            <br />&nbsp;&nbsp; đ) Các đơn vị sự nghiệp trực thuộc:

            <br />&nbsp;&nbsp;- Trung tâm Bảo tồn, phát triển y dược cổ truyền.

            <br />3. Cơ chế hoạt động

            <br />&nbsp;&nbsp;a) Cục Quản lý Y, Dược cổ truyền hoạt động theo chế độ thủ trưởng;

            <br />&nbsp;&nbsp;b) Chức năng, nhiệm vụ, quyền hạn của Văn phòng Cục và các phòng, tổ chức thuộc Cục do Cục trưởng quy định trên cơ sở chức năng, nhiệm vụ, quyền hạn của Cục đã được Bộ trưởng Bộ Y tế giao. Mối quan hệ giữa Văn phòng Cục, các phòng, tổ chức thuộc Cục do Cục trưởng quy định;

            <br />&nbsp;&nbsp;c) Việc bổ nhiệm, miễn nhiệm các chức danh lãnh đạo Văn phòng Cục và lãnh đạo các phòng, tổ chức thuộc Cục được thực hiện theo quy định của pháp luật và theo phân cấp của Bộ Y tế;

            <br />&nbsp;&nbsp;d) Các chức danh lãnh đạo Cục, lãnh đạo Văn phòng Cục và lãnh đạo các phòng, tổ chức thuộc Cục được hưởng phụ cấp theo quy định của pháp luật.

            <br />4. Biên chế

            <br />&nbsp;&nbsp;Biên chế của Cục Quản lý Y, Dược cổ truyền được xác định theo quy định của pháp luật về vị trí việc làm và được điều chỉnh theo nhu cầu vị trí việc làm do Bộ trưởng Bộ Y tế quyết định trên cơ sở đề xuất của Cục trưởng Cục Quản lý Y, Dược cổ truyền.

            <br />5. Kinh phí

            <br />&nbsp;&nbsp;Kinh phí hoạt động của Cục Quản lý Y, Dược cổ truyền do ngân sách nhà nước cấp và các nguồn kinh phí hợp pháp khác.
            </p>
            <h5><b> CHỨC NĂNG NHIỆM VỤ </b></h5>
            <p>
            Cục Quản lý Y, Dược cổ truyền là cục chuyên ngành thuộc Bộ Y tế, thực hiện chức năng tham mưu, giúp Bộ trưởng Bộ Y tế quản lý nhà nước và tổ chức thực thi pháp luật, chỉ đạo điều hành các hoạt động chuyên môn, nghiệp vụ về lĩnh vực y dược cổ truyền trong phạm vi cả nước với mục tiêu kế thừa, bảo tồn và phát triển y dược học cổ truyền, kết hợp với y dược học hiện đại trong việc chăm sóc và bảo vệ sức khoẻ nhân dân; xây dựng nền y dược Việt Nam hiện đại, khoa học, dân tộc và đại chúng.
            </p>
          </Col>
        </Row>
      </Container>
  )
}

export default GioiThieu1
