export const composeValidators = (...validators) => value =>
  validators.reduce((error, validator) => error || validator(value), undefined)
export const required = value => (value != null && `${value}`.length > 0 ? null : 'Vui lòng nhập trường này');
export const otp = value => (value.toString().length !== 6 ? 'OTP có 6 ký tự' : null);
export const email = value => (value && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,6}$/i.test(value) ? 'Yêu cầu nhập đúng định dạng email' : null);
export const mobile = value => (value && !/^0[1-9]\d{8}$/i.test(value) ? 'Yêu cầu nhập đúng định dạng số điện thoại' : null);
export const cmndCCCD = value => {
  if (!value) {
    return null;
  }
  if (!/^[0-9.]/i.test(value)) return 'CCCD/CMND phải bắt đầu bằng số hoặc ký tự "."'
  if (!/^[0-9.]\d*$/i.test(value)) return 'CCCD/CMND chỉ chứa số và ký tự "."'
  if (/^\./i.test(value) && value.length !== 10 && value.length !== 13) {
    return 'CCCD/CMND bắt đầu bằng dấu "." phải có 10 hoặc 13 ký tự'
  }
  if (/^\d/i.test(value) && value.length !== 9 && value.length !== 12) {
    return 'CCCD/CMND bắt đầu bằng số phải có 9 hoặc 12 ký tự'
  }
  return null;
}
export const person_name = value =>
  value &&
    !/^[A-Za-zÀÁÂÃÈÉÊÌÍÒÓÔÕÙÚĂĐĨŨƠàáâãèéêìíòóôõùúăđĩũơƯĂẠẢẤẦẨẪẬẮẰẲẴẶẸẺẼỀỀỂưăạảấầẩẫậắằẳẵặẹẻẽềềểếỄỆỈỊỌỎỐỒỔỖỘỚỜỞỠỢỤỦỨỪễệỉịọỏốồổỗộớờởỡợụủứừỬỮỰỲỴÝỶỸửữựỳỵỷỹ ]{5,30}$/i.test(
      value
    )
    ? 'Vui lòng nhập đúng định dạng và từ 5 đến 30 ký tự'
    : null;
export const passport = value => (value && !/^[a-zA-Z0-9]{1,20}$/i.test(value)) ? 'Hộ chiếu chỉ chứa chữ cái và số có độ dài từ 1-20 ký tự' : null

const exporter = {
  composeValidators, required, otp, email, cmndCCCD, person_name, mobile, passport
}
export default exporter
