import React from 'react'
import { Row, Col, Container } from 'reactstrap'
import './style.scss'
import classes from './lanhdao.module.scss'
import image1 from '_assets/nguyen-the-thinh.jpeg'
import image2 from '_assets/anh-ngoc.jpg'
import image3 from '_assets/nguyen-ngoc-tuan.jpg'

const leaders = [{
  avatar: image1,
  fullname: 'PGS.TS. NGUYỄN THẾ THỊNH',
  gender: 'MALE',
  position: 'Cục Trưởng',
  description: 'Cục Trưởng Cục Y, Dược Cổ Truyền.'
}, {
  avatar: image3,
  fullname: 'ThS.BS. NGUYỄN NGỌC TUẤN',
  gender: 'MALE',
  position: 'Phó Cục Trưởng',
  description: 'Phó Cục Trưởng Cục Y, Dươc Cổ Truyền'
}, {
  avatar: image2,
  fullname: 'TS.DS. TRẦN MINH NGỌC',
  gender: 'MALE',
  position: 'Phó Cục Trưởng',
  description: 'Phó Cục Trưởng Cục Y, Dược Cổ Truyền.'
}]

function LanhDao(props) {
  return (
    <div style={{ paddingBottom: '60px'}} className='pt-5'>
      <Container>
        <Row>
          <Col xs={12} className='text-center'>
            <h4><b> LÃNH ĐẠO ĐƯƠNG NHIỆM </b></h4>
          </Col>
          <Col xs={12} className='text-center mb-20' style={{marginBottom: '24px'}}>
            <b> Danh sách những người được nhà nước tin tưởng giao trọng trách lèo lái con thuyền của cục Y, Dược cổ truyền </b>
          </Col>
        </Row>
        { leaders.map((item, i) => (
          <div className={classes.row}
            key={i}
          >
            <img src={item.avatar} className={classes.image} alt='img'/>
            <div className={classes.info}>
              <p className={classes.name}>
                ({item.gender === 'MALE' ? 'Ông' : 'Bà'})
                <strong>
                  { item.fullname } 
                </strong>
              </p>
              <p className={classes.position}>
                { item.position }
              </p>
              <p className={classes.description}>
                { item.description }
              </p>
            </div>
          </div>
        )) }
        
      </Container>
    </div>
  )
}

export default LanhDao
